// Initialize
window.addEventListener("DOMContentLoaded", initialize);

function initialize() {
  console.log("Welcome! ðŸ‘‹");
  activateStickyScrollPadding();
  getBookmarklet();
  getCopyText();
  getContactEmail();
  getNetworkStatus();
}

// Scrolling
function activateStickyScrollPadding() {
  const navItem = document.querySelector(".navbar");
  if (navItem) {
    const navItemHeight = navItem.offsetHeight;

    document.documentElement.style.setProperty(
      "--scroll-padding",
      `${navItemHeight}px`
    );
  }
}

// Bookmarklet
function getBookmarklet() {
  let linkItems = document.querySelectorAll(".bookmarklet__link");
  let linkArray = Array.from(linkItems);

  linkArray.forEach((link) => {
    let domain = location.hostname;

    if (domain !== "localhost") {
      domain = location.href.replace(location.hash, "");
    } else {
      domain = `${location.origin}/`;
    }

    let url;
    let bookmarkStart = `javascript:(function(){var t=document.createElement("script");t.id="fb-collect-sc",t.type="text/javascript",`;
    let bookmarkEnd = `,document.body.appendChild(t)}())`;

    switch (link.id) {
      case "youtube":
        url = `${domain}dist/collector-youtube.min.js`;
        link.setAttribute(
          "href",
          `${bookmarkStart}t.src="${url}"${bookmarkEnd}`
        );
        break;
      case "facts":
        url = `${domain}dist/collector-facts.min.js`;
        link.setAttribute(
          "href",
          `${bookmarkStart}t.src="${url}"${bookmarkEnd}`
        );
        break;
      case "hundeo":
        url = `${domain}dist/collector-hundeo.min.js`;
        link.setAttribute(
          "href",
          `${bookmarkStart}t.src="${url}"${bookmarkEnd}`
        );
        break;
      case "breed":
        url = `${domain}dist/collector-breed.min.js`;
        link.setAttribute(
          "href",
          `${bookmarkStart}t.src="${url}"${bookmarkEnd}`
        );
        break;
    }
  });
}

// Clipboard
function getCopyText() {
  let copyItems = document.querySelectorAll(".content__clip");
  let copyArray = Array.from(copyItems);

  copyArray.forEach((copy) => {
    copy.addEventListener("click", () => {
      let rawText = copy.previousElementSibling.textContent;
      let copyText = rawText.replace(/\n/g, "").replace(/'/g, '"');
      navigator.clipboard.writeText(copyText);
    });
  });
}

// Email
function getContactEmail() {
  let contactItems = document.querySelectorAll("[data-email]");
  let contactArray = Array.from(contactItems);

  contactArray.forEach((contact) => {
    document.addEventListener("click", (event) => {
      getContactEvent(event);
    });

    document.addEventListener("keydown", (event) => {
      if (event.key === "Enter") {
        getContactEvent(event);
      }
    });

    function getContactEvent(event) {
      if (contact.id == event.target.id) {
        let emailData = "helloATfebreroDOTinfo";
        emailData = emailData.replace(/AT/, "@").replace(/DOT/, ".");
        contact.setAttribute("data-email", "true");
        contact.setAttribute("href", `mailto:${emailData}`);
      } else {
        contact.setAttribute("data-email", "false");
        contact.removeAttribute("data-value");
        contact.removeAttribute("href");
        contact.removeAttribute("rel");
        contact.removeAttribute("target");
      }
    }
  });
}

// Network
function getNetworkStatus() {
  let status = document.querySelector("#status");
  let online = document.querySelector("#online");
  let offline = document.querySelector("#offline");

  function updateNetworkStatus() {
    let condition = navigator.onLine ? "online" : "offline";

    if (condition === "offline") {
      status.setAttribute("data-open", "true");
      offline.setAttribute("data-open", "true");
      online.setAttribute("data-open", "false");
    } else {
      status.setAttribute("data-open", "true");
      online.setAttribute("data-open", "true");
      offline.setAttribute("data-open", "false");
    }

    setTimeout(() => {
      online.setAttribute("data-open", "false");
      status.setAttribute("data-open", "false");
    }, 6000);
  }

  window.addEventListener("online", updateNetworkStatus);
  window.addEventListener("offline", updateNetworkStatus);
}
